	module.exports = {"data":{"markdownRemark":{"html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 405px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 28.39506172839506%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"BOSH logo\"\n        title=\"\"\n        src=\"/static/bosh-51ff2892ab422e1a96eac6516204c63e-2b26b.png\"\n        srcset=\"/static/bosh-51ff2892ab422e1a96eac6516204c63e-e3199.png 240w,\n/static/bosh-51ff2892ab422e1a96eac6516204c63e-2b26b.png 405w\"\n        sizes=\"(max-width: 405px) 100vw, 405px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>After working on client projects for my first few months at Pivotal Labs, I was asked to work on BOSH.\nBOSH had been started at Google and VMware and was still actively developed at Pivotal.</p>\n<h3>Contributions</h3>\n<p>I helped the BOSH team do some rewrites of parts of the codebase that were not performing well and needed to be rethought for new goals.\nWe also worked on a bunch of new features and maintenance.\nWe did a lot of work on our CI pipeline to keep this complex software with multiple targets stable for our users.</p>\n<h3>Description</h3>\n<p>BOSH consists of many components, but primarily:</p>\n<ul>\n<li>a director service that coordinates actions involving the distributed systems it manages</li>\n<li>an agent that runs on every VM BOSH creates via its custom machine images</li>\n<li>a messaging system (<a href=\"http://nats.io\">NATS</a>) enabling communication between all the components of the system</li>\n</ul>\n<p>BOSH is written mostly in Ruby, with some components in Go.\nIt makes use of several infrastructure services: AWS, vSphere, and OpenStack in particular.</p>","fields":{"project":{"title":"BOSH","subtitle":"An open source tool chain for release engineering, deployment and lifecycle management of large scale distributed services","status":"contracted","createdAtTimestamp":"2014-02-01T00:00:00.000Z","createdAt":"February 2014","deployedUrl":"http://bosh.io","endedAtTimestamp":"2014-10-31T00:00:00.000Z","endedAt":"October 2014","sourceUrl":"https://github.com/cloudfoundry/bosh"}}},"site":{"siteMetadata":{"title":"Adam Stegman"}}},"pathContext":{"slug":"/projects/bosh"}}

